<script>
export default {
  data:()=>({
      t:false,
      a:false,
      b:false,
      c:false,
      d:false,
      e:false
  }),
   methods: {
    fn() {
      if (document.documentElement.scrollTop >= 1969) {
        this.t = true;
      }
      if (document.documentElement.scrollTop >= 2067) {
        this.a = true;
      }
      if (document.documentElement.scrollTop >= 2267) {
        this.b = true;
      }
      if (document.documentElement.scrollTop >= 2467) {
        this.c = true;
      }
      if (document.documentElement.scrollTop >= 2667) {
        this.d = true;
      }
      if (document.documentElement.scrollTop >= 2867) {
        this.e = true;
      }
    }
  },
  mounted() {
    window.addEventListener("scroll", this.fn);
  }
};
