<style scoped>
#box {
  width: 100%;
  overflow: hidden;
}
.title {
  width: 100%;
  line-height: 50px;
  font-size: 36px;
  text-align: center;
}
.title p {
  font-size: 20px;
  line-height: 26px;
  color: #7e7e7e;
}
.title .wire {
