}
.newsList li{
    margin: 20px 0;
    overflow: hidden;
}
.newsContent {
  height: 162px;
  width: 620px;
  float: left;
  margin-left: 50px;
}
.newsContent p a{
    text-decoration: none;
    color: rgb(100, 100, 100);
    font-size: 20px;
}
.newsContent p a:hover{
    color: rgb(103, 252, 170);
}
.newsContent p:nth-of-type(2){
        color: rgb(194, 194, 194);
}
.newsContent p:nth-of-type(3){
        color: rgb(228, 228, 228);
        margin-top: 30px;
}
.title-enter,
.options-enter, 
.options2-enter, 
.options3-enter, 
.options4-enter,
.options5-enter {
  transform: translateY(100px);
  opacity: 0;
}
.title-enter-active {
  transition: 1s ease 0.4s;
}
.options-enter-active {
  transition: 1s ease 0.6s;
}
</style>



