<template>
    <transition name="top">
    <div id="box" @click="fn" v-show="t">
        <img src="@/assets/top.gif" alt="">
    </div>
    </transition>
</template>

<script>
export default {
    data:()=>({
        t:false
    }),
methods:{
    fn(){
        let p=setInterval(()=>{
          if(document.documentElement.scrollTop){
            document.documentElement.scrollTop-=10;
          } else{
            clearInterval(p)
          }
        })
    },
    fn2(){
         if (document.documentElement.scrollTop >= 1000) {
             if(!this.t){
                  this.t = true;
             }
       
      }
      if(document.documentElement.scrollTop <= 1000){
          this.t = false;
      }
    }
},
mounted(){
    window.addEventListener('scroll',this.fn2) 
}
}
</script>

<style scoped>
    #box{
        width: 58px;
        height: 58px;
        position: fixed;
        bottom: 10px;
        right: 10px;
        overflow: hidden;
        border-radius: 50%;
    }
    #box img{
        position: absolute;
        width: 100px;
        left: -22px;
        top: -21px;
    }
    @keyframes topshow{
        0%{
           transform: translateX(50px);
           opacity: 0; 
        }
        100%{
           transform: translateX(0px);
           opacity: 1; 
        }
    }
    @keyframes xiaoshi{
        0%{
            transform: translateY(0px);
        opacity: 1;
        }
        100%{
            transform: translateY(-700px);
        opacity: 0;
        }
    }
    .top-enter-active {
        animation: topshow 1S ease
    }
     .top-leave-active{
         animation: xiaoshi 1s ease
     }
</style>



