<template>
  <ul id="Neck">
      <li><a href="">
          <div class="circle" :style="{background:oStyle}">
              <img src="../assets/1.png" alt="">
          </div>
          <p>家装风格</p>
          <p>还您一个温馨的家</p>
          </a></li>
      <li><a href="">
          <div class="circle" :style="{background:oStyle}">
              <img src="../assets/2.png" alt="">
          </div>
          <p>家装风格</p>
          <p>还您一个温馨的家</p>
          </a></li>
      <li><a href="">
          <div class="circle" :style="{background:oStyle}">
              <img src="../assets/3.png" alt="">
          </div>
          <p>家装风格</p>
          <p>还您一个温馨的家</p>
          </a></li>
      <li><a href="">
          <div class="circle" :style="{background:oStyle}">
              <img src="../assets/4.png" alt="">
          </div>
          <p>家装风格</p>
          <p>还您一个温馨的家</p>
          </a></li>
  </ul>
</template>

<script>
import Bus from "../bus";
export default {
    data:()=>({
        'a':0
    }),
    mounted(){
        Bus.$on('gevent',(data)=>{
            this.a=data;
        })
    },
    computed:{
       oStyle:function(){
           if(this.a==0){
               return '#628be7';
           }
           if(this.a==1){
               return '#e2dfda';
           }
           if(this.a==2){
               return '#779690';
           }
       }
    }
};
</script>

<style scoped>
#Neck {
    display: flex;
  width: 1280px;
  height: 200px;
  background: #eaeaea;
  justify-content: space-around;
  align-items: center;
  margin: auto;
}
#Neck li{
    width: 170px;
    height: 160px;
    background: white;
    transition: .3s;
    text-align: center;
}
a{
    text-decoration: none;
}
#Neck li a{
    display: block;
    width: 100%;
    height: 100%;
}
#Neck li:hover{
    margin-top: -15px;
    box-shadow: 0px 5px 10px 0 #b3b3b3;
    border-radius: 10px;
    
}
#Neck li .circle{
    width: 74px;
    height: 74px;
    border-radius:50%;
    background: #779690;
    margin:12px auto;
    line-height: 68px;
    transition: .5s;
}
#Neck li .circle img{
    vertical-align: middle;
}
#Neck li p{
    margin: 0;
}
#Neck li p:nth-of-type(1){
    font-size: 20px;
    color: #2f2f2f;
}
#Neck li p:nth-of-type(2){
    font-size: 12px;
    color: #9d9d9d;
}
</style>



