<template>
  <div id="box">
    <transition name="title">
      <div class="title" v-show="t">
        <span class="wire"></span>
        <span>设计团队</span>
        <span class="wire"></span>
        <p>全国一线顶尖设计师为您设计</p>
      </div>
    </transition>
    <!--  轮播 -->
    <transition name="options">
      <div class="content" v-show="t">
        <swiper :options="swiperOption">
          <!-- slides -->
          <swiper-slide>
            <a href>
              <img src="../../static/stylistImg/6.png" alt>
            </a>
          </swiper-slide>
          <swiper-slide>
            <a href>
              <img src="../../static/stylistImg/5.png" alt>
            </a>
          </swiper-slide>
          <swiper-slide>
            <a href>
              <img src="../../static/stylistImg/4.png" alt>
            </a>
          </swiper-slide>
          <swiper-slide>
            <a href>
              <img src="../../static/stylistImg/3.png" alt>
            </a>
          </swiper-slide>
          <swiper-slide>
            <a href>
              <img src="../../static/stylistImg/2.png" alt>
            </a>
          </swiper-slide>
          <swiper-slide>
            <a href>
              <img src="../../static/stylistImg/1.png" alt>
            </a>
          </swiper-slide>
          <!-- Optional controls -->
        </swiper>
        <p class="arrows">
          <img src="../../static/stylistImg/8.png" alt>
        </p>
        <div class="swiper-pagination" slot="pagination"></div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "carrousel",
  data() {
    return {
      swiperOption: {
        loop: true, // 循环模式选项
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        autoplay: true,
        effect: "coverflow",
        slidesPerView: 3,
        centeredSlides: true
      },
      t: false
    };
  },
  methods: {
    fn() {
      if (document.documentElement.scrollTop >= 1300) {
        this.t = true;
      }
    }
  },
  mounted() {
    window.addEventListener("scroll", this.fn);
  }
};
</script>

<style scoped>
#box {
  width: 100%;
  height: 680px;
  margin-top: 30px;
}
.title {
  width: 100%;
  line-height: 50px;
  font-size: 36px;
  text-align: center;
}
.title p {
  font-size: 20px;
  line-height: 26px;
  color: #7e7e7e;
}
.title .wire {
  display: inline-block;
  width: 114px;
  height: 1px;
  background: black;
  vertical-align: middle;
}
.content {
  width: 840px;
  height: 510px;
  margin: auto;
  padding-top: 20px;
  border-radius: 10px;
  background: #f5f4f4;
}
.content img {
  width: 280px;
  height: 314px;
  border-radius: 10px;
}
.swiper-pagination {
  width: 840px;
  margin: auto;
}
.arrows {
  text-align: center;
}
@keyframes Updown {
  0% {
    transform: translateY(-10px);
  }
  50% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(10px);
  }
}
.arrows img {
  width: 30px;
  height: 30px;
  animation: Updown 1s linear infinite alternate;
}
.title-enter,
.options-enter {
  transform: translateY(100px);
  opacity: 0;
}
.title-enter-active {
  transition: 1s ease 0.4s;
}
.options-enter-active {
  transition: 1s ease 0.5s;
}
</style>



