<template>
  <div id="box">
    <transition name="title">
    <div class="title" v-show="t">
      <span class="wire"></span>
      <span>装修效果</span>
      <span class="wire"></span>
    </div>
    </transition>
    <transition name="options">
    <div class="options" v-show="t">
      <ul>
        <li>
          <a href="#" @click.prevent="oIndex=0" :class="{'onClick':oIndex==0}">客厅</a>
        </li>
        <li>
          <a href="#" @click.prevent="oIndex=1" :class="{'onClick':oIndex==1}">厨房</a>
        </li>
        <li>
          <a href="#" @click.prevent="oIndex=2" :class="{'onClick':oIndex==2}">卫生间</a>
        </li>
        <li>
          <a href="#" @click.prevent="oIndex=3" :class="{'onClick':oIndex==3}">卧室</a>
        </li>
        <li>
          <a href="#" @click.prevent="oIndex=4" :class="{'onClick':oIndex==4}">阳台</a>
        </li>
      </ul>
    </div>
    </transition>
    <transition name="caseImg">
    <div class="caseImg" v-show="t">
      <div>
        <a href>
          <p>现代风格{{Title[oIndex]}}效果图</p>
          <img :src="Url[oIndex][0]" alt>
        </a>
      </div>
      <div>
        <a href>
          <p>简约风格{{Title[oIndex]}}效果图</p>
          <img :src="Url[oIndex][1]" alt>
        </a>
      </div>
      <div>
        <a href>
          <p>欧式风格{{Title[oIndex]}}效果图</p>
          <img :src="Url[oIndex][2]" alt>
        </a>
      </div>
    </div>
    </transition>
  </div>
</template>

<script>
export default {
  data: () => ({
    Url: [
      [
        require("../../static/effectImg/1.png"),
         require("../../static/effectImg/2.png"),
         require("../../static/effectImg/3.png")
      ],
      [
         require("../../static/effectImg/4.png"),
         require("../../static/effectImg/5.png"),
         require("../../static/effectImg/6.png")
      ],
      [
         require("../../static/effectImg/7.png"),
         require("../../static/effectImg/8.png"),
         require("../../static/effectImg/9.png")
      ],
      [
         require("../../static/effectImg/10.png"),
         require("../../static/effectImg/11.png"),
         require("../../static/effectImg/12.png")
      ],
      [
         require("../../static/effectImg/13.png"),
         require("../../static/effectImg/14.png"),
         require("../../static/effectImg/15.png")
      ]
    ],
    oIndex: 0,
    off: false,
    Title:['客厅','厨房','卫生间','卧室','阳台'],
    t:false
  }),
  methods:{
      fn(){
          if(document.documentElement.scrollTop>=700){
          this.t=true;
        }
      }
  },
  mounted(){
     window.addEventListener('scroll',this.fn)
    }
};
</script>

<style scoped>
#box {
  width: 100%;
/*   outline: 1px solid red; */
  height: 600px;
  color: #373737;
  background: #eeeeee;
  padding-top: 50px;
}
.title {
  width: 100%;
  height: 50px;
  line-height: 50px;
  font-size: 36px;
  text-align: center;
}
.title .wire {
  display: inline-block;
  width: 114px;
  height: 1px;
  background: black;
  vertical-align: middle;
}
a {
  text-decoration: none;
  color: #969696;
  font-size: 20px;
}
.title span:nth-of-type(2) {
  padding: 0 20px;
}
.options {
  margin-top: 30px;
  width: 100%;
}
.options ul {
  width: 450px;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
}
.options ul li {
  float: left;
}
.options ul li a {
  border: 2px solid #c2c2c2;
  border-radius: 10px;
  padding: 0 6px;
  transition: 0.2s;
}
.onClick {
  background: #fb7905;
  color: white;
  border-color: #fb7905 !important;
}
.options ul li a:hover {
  background: #fb7905;
  color: white;
  border-color: #fb7905;
}
.caseImg {
  width: 100%;
  height: 400px;
  display: flex;
  justify-content: center;
  margin-top: 80px;
}
.caseImg div {
  margin: 0 20px;
  width: 280px;
  height: 320px;
  position: relative;
  overflow: hidden;
  border: 8px solid rgb(214, 214, 214);
  transition: 0.2s;
}
.caseImg div img {
  width: 280px;
  height: 320px;
  position: absolute;
  left: 50%;
  margin-left: -140px;
  transition: 0.7s ease-out;
}
.caseImg div:hover img {
  transform: scale(1.1);
}
.caseImg div:hover {
  transform: translateY(-10px);
  box-shadow: 0 5px 10px 0 rgb(136, 136, 136);
}
.caseImg div p {
  position: absolute;
  width: 100%;
  height: 60px;
  z-index: 10;
  font-size: 16px;
  text-align: center;
  line-height: 60px;
  margin: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  color: white;
}
.title-enter,.options-enter,.caseImg-enter{
  transform: translateY(100px);
  opacity: 0;
}
.title-enter-active{
  transition: 1s ease .4s
}
.options-enter-active{
  transition: 1s ease .5s
}
.caseImg-enter-active{
  transition: 1s ease .6s
}
</style>



