<template>
  <header>
    <a href class="logo">
      <img src="../assets/logo.png" alt>
    </a>
    <ul>
      <li>
        <a href>首页</a>
      </li>
      <li>
        <a href>风格案例</a>
      </li>
      <li>
        <a href>功能区域</a>
      </li>
      <li>
        <a href>设计团队</a>
      </li>
      <li>
        <a href>工地形象</a>
      </li>
      <li>
        <a href>环保主材</a>
      </li>
      <li>
        <a href>联系我们</a>
      </li>
    </ul>
    <div class="hleft">
      <form class="Search">
        <label for="search" @click="Search"></label>
        <transition name="move">
          <input type="text" id="search" placeholder="请输入搜索内容" v-show="off">
        </transition>
      </form>
      <div class="User">
        <a href>登陆</a>
        <span>/</span>
        <a href>注册</a>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  data: () => ({
    off: false
  }),
  methods: {
    Search() {
      this.off = true;
    }
  }
};
</script>

<style scoped>
header {
  box-sizing: border-box;
  width: 1280px;
  height: 60px;
  line-height: 60px;
  margin: auto
}
.logo img {
  vertical-align: middle;
}
.logo {
  float: left;
}
ul {
  margin-left: 40px;
  float: left;
  list-style: none;
  width: 784px;
  height: 100%;
}
ul li {
  float: left;
  width: 112px;
  text-align: center;
}
ul li a {
  display: block;
  font-weight: bold;
}
ul li:hover {
  background: #e4e4e4;
}
a {
  text-decoration: none;
  color: rgb(97, 97, 97);
}
.hleft {
  width: 316px;
  height: 100%;
  float: left;
  font-weight: bold;
}
.User {
  height: 40px;
  margin: 10px 0;
  border-left: 1px solid #d8d8d8;
  padding-left: 10px;
  float: left;
  line-height: 40px;
  color: rgb(97, 97, 97);
}
.Search {
  float: left;
  box-sizing: border-box;
  padding-left: 20px;
  padding-right: 10px;
}
.Search label {
  width: 24px;
  height: 24px;
  background: #999999;
  vertical-align: middle;
  float: left;
  margin-top: 18px;
  background: url("../assets/search.png");
  background-size: 24px;
  cursor: pointer;
}
#search {
  float: left;
  outline: none;
  background: #dddddd;
  border: none;
  border-radius: 4px 4px 4px 4px;
  height: 24px;
  margin-top: 18px;
  margin-left: 10px;
  padding: 0 5px;
  font-size: 12px;
}
#search:focus {
  border-color: aqua;
}
.move-enter {
  width: 0;
  opacity: 0;
}
.move-enter-to {
  width: 150px;
  opacity: 1;
}
.move-enter-active {
  transition: 0.2s linear;
}
</style>



