<template>
    <div id="banner">
        <div v-show="false">
        {{oIndex}}
        </div>
        <div class="box" ref="Box">
            <a href="">
            <img src="../assets/banner3.jpg" alt="">
            </a>
            <a href="">
            <img src="../assets/banner1.jpg" alt="">
            </a>
            <a href="">
            <img src="../assets/banner2.jpg" alt="">
            </a>
            <a href="">
            <img src="../assets/banner3.jpg" alt="">
            </a>
             <a href="">
            <img src="../assets/banner1.jpg" alt="">
            </a>
        </div>
        <i id="right" ref="right"></i>
        <i id="left" ref="left"></i>
        <p ref="point">
            <span></span>
            <span></span>
            <span></span>
        </p>
    </div>
</template>

<script>
import Bus from "../bus";
export default {
    data:()=>({
        'oIndex':0
    }),
    updated(){
        Bus.$emit('gevent',this.oIndex);
    },
   mounted(){
       let oCen=this.$refs.Box;
       let aImg=this.$refs.Box.querySelectorAll("img");
       let oSpan=this.$refs.point.querySelectorAll("span");
       let oLeft=this.$refs.left
       let oRight=this.$refs.right
       let oVue=this;
         var t = 0;
        var tim = 0;
        var a = 0;
        var er = 0;
//a代表box b代表图片 c代表步长 off代表左右
        function mov(a, b, c, off) {
           
              let  y = 0
            
            
              let  lk = 0
           
            clearInterval(er);
            er = setInterval(function () {
                clearInterval(tim);
                if (off == "right") {
                    t = c;
                }
                if (off == "left") {
                    t = -c;
                }
                a.style.left = parseInt(getComputedStyle(a).left) + t + "px";
               
                    dian();
                    if (parseInt(getComputedStyle(a).left) % parseInt(getComputedStyle(b[0]).width) == y) {
                        ret(3);
                        clearInterval(er);
                    }
                if (parseInt(getComputedStyle(a).left) == -parseInt(getComputedStyle(b[0]).width) * (b.length -
                        1)) {
                    a.style.left = -parseInt(getComputedStyle(b[0]).width) + "px";
                }
                if (parseInt(getComputedStyle(a).left) == 0) {
                    a.style.left = -((b.length - 2) * parseInt(getComputedStyle(b[0]).width)) + "px";
                }
            }, 1)

        }
        function ret(n) {
            clearInterval(tim);
            tim = setInterval(function () {
                mov(oCen, aImg, 2, "left");

            }, 1000 * n)
        }
        ret(3);
        oCen.onmouseover = function () {
            a
            clearInterval(tim);
        }
        oCen.onmouseout = function () {
            ret(3);
        }
        oLeft.onclick = function () {
            if (parseInt(getComputedStyle(oCen).left) % parseInt(aImg[0].width) == 0) {
                clearInterval(tim);
                mov(oCen, aImg, 5, "right");
                ret(3)
            }
        }
        oRight.onclick = function () {
            if (parseInt(getComputedStyle(oCen).left) % parseInt(aImg[0].width) == 0) {
                clearInterval(tim);
                mov(oCen, aImg, 5, "left");
                ret(3)
            }
        }

        function qk(s) {
            for (var m = 0; m < oSpan.length; m++) {
                s[m].style.background = "";
            }
        }
    oSpan[0].style.background = "#fb7905";
        function dian() {
            let Dlen=oSpan.length;
            for (var m = 0; m < Dlen; m++) {
                if (parseInt(getComputedStyle(oCen).left) == -(m + 1) * parseInt(getComputedStyle(aImg[0]).width)) {
                    qk(oSpan);
                    oVue.oIndex=m;
                    oSpan[m].style.background = "#fb7905";
                }
            }
            if (parseInt(getComputedStyle(oCen).left) == -(Dlen+1) * parseInt(getComputedStyle(aImg[0]).width)) {
                qk(oSpan);
                 oVue.oIndex=0
                oSpan[0].style.background = "#fb7905";
            }
            if (parseInt(getComputedStyle(oCen).left) == 0) {
                qk(oSpan);
                oVue.oIndex=2;
                oSpan[Dlen-1].style.background = "#fb7905";
            }
        }
        for (var c = 0; c < oSpan.length; c++) {
            oSpan[c].index = c + 1;
            oSpan[c].onclick = function () {
                if (parseInt(getComputedStyle(oCen).left) % parseInt(aImg[0].width) == 0) {
                    clearInterval(tim);
                    oCen.style.left = -parseInt(getComputedStyle(aImg[0]).width) * this.index + "px";
                    dian();
                    ret(3)
                }
            }
        }
   }
}
</script>

<style scoped>
    #banner{
        overflow: hidden;
        width: 1280px;
        height: 400px;
        margin: auto;
        background: blue;
        position:relative;
    }
    .box{
        width: 6400px;
        height: 100%;
        position: absolute;
        left: -1280px;
    }
    .box a{float: left;}
     p {
            width: 300px;
            line-height: 50px;
            position: absolute;
            bottom:0;
            left: 50%;
            margin-left: -150px;
        }

        p span {
            width: 40px;
            height: 2px;
            background: #ffffff;
            opacity: .8;
            float: left;
            cursor: pointer;
            margin: 0 16px;
        }
        i {
            position: absolute;
            width: 50px;
            height: 50px;
            top: 50%;
            margin-top: -25px;
            cursor: pointer;
            opacity: .8;
            transition: .3s;
            background: url("../assets/left.png");
            background-size:100px 50px;
        }
        i:hover {
            opacity: 1;
        }
        i:nth-of-type(1) {
            right: -50px;
        }
        i:nth-of-type(2) {
            background-position:-50px 0;
            left: -50px;
        }
        #banner:hover i:nth-of-type(1) {
            right:50px;
        }
        #banner:hover i:nth-of-type(2) {
            left: 50px;
        }

</style>



